@import "./font-face-technical";
@import "./font-face-website";

// Directly apply style to body of the application
.se-font-technical, .se-font, .se-app-body:not([class*="se-font-"]) {
  // default the font family to nunito if the class "se-font-*" does not exist. 
  // To be retro-compatible with app that depended on "body" to get the font-family 
  font-family: Nunito, Roboto, monospace;
}
.se-font-website {
  font-family: Arial_Rounded, Arial, "Helvetica Neue", Helvetica, sans-serif;
}
// Nunito font-weight: bold is too bold.
h1,h2, h3, h4, h5, h6 {
  font-weight: 500;
}
