$se-fonts-path: "../fonts" !default;

// ~~~~~~~~~~~~
// Technical application
// ~~~~~~~~~~~~~

@font-face {
  font-family: "Nunito";
  font-weight: 400;
  font-style: normal;
  src: url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Nunito Regular"), local("Nunito-Regular"),
    url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("#{$se-fonts-path}/nunito/nunito-v9-latin-regular.svg#Nunito") format("svg"); /* Legacy iOS */
}

/* nunito-300 - latin */
@font-face {
  font-family: "Nunito";
  font-weight: 300;
  font-style: normal;
  src: url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.eot"); /* IE9 Compat Modes */
  src: local("Nunito Light"), local("Nunito-Light"),
    url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.woff") format("woff"),
    /* Modern Browsers */ url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("#{$se-fonts-path}/nunito/nunito-v9-latin-300.svg#Nunito") format("svg"); /* Legacy iOS */
}